<template>
  <section>
    <loader :is-visible="isLoading"></loader>

 
    <div class="btn-group btn-group-toggle labelalign">
      <button v-on:click="selectToday" :class="dateType == 'DD' ? 'bg-success text-white button-container left-rounded': 'text-black button-container left-rounded' "> {{$t('Day')}}</button>
      <button v-on:click="selectWeekly" :class="dateType == 'WK' ? 'bg-success text-white button-container' : 'text-black button-container'"> {{$t('Weekly')}}</button>
      <button v-on:click="selectMonthly" :class="dateType == 'MM' ? 'bg-success text-white button-container right-rounded': 'text-black button-container right-rounded' "> {{$t('Monthly')}} </button>
    </div>

    <div class="col-sm-12 col-md-1 col-xl-2 labelalign">
      <div class="input-group-prepend">
        <vc-date-picker v-if="dateType == 'DD'" v-bind:name="fromDate" :minute-increment="5" class="timedate input-group"
          v-model="fromDate" @input="onDateChange">
          <template v-slot="{ inputValue, inputEvents, togglePopover }">
            <input placeholder="M/D/YYYY" class="form-control" :value="inputValue" v-on="inputEvents" />
            <div class="input-group-append" v-on:click="togglePopover">
              <span class="input-group-text">
                <em class="fa fa-calendar"></em>
              </span>
            </div>
          </template>
        </vc-date-picker>

        <vc-date-picker v-if="dateType == 'WK'" is-range :minute-increment="5" class="timedate input-group"
          v-model="weekDate" @input="onWeekDateChange">
          <template v-slot="{ inputValue, inputEvents, togglePopover }">
            <input class="form-control" :value="inputValue.start + ' - ' + inputValue.end" v-on="inputEvents" />
            <div class="input-group-append" v-on:click="togglePopover">
              <span class="input-group-text">
                <em class="fa fa-calendar"></em>
              </span>
            </div>
          </template>
        </vc-date-picker>

        <vc-date-picker v-if="dateType == 'MM'" is-range class="timedate input-group" v-model="monthRange"
          @input="onMonthSelected">
          <template v-slot="{ inputValue, inputEvents, togglePopover }">
            <input placeholder="M/D/YYYY" class="form-control" :value="inputValue.start + ' - ' + inputValue.end"
              v-on="inputEvents" />
            <div class="input-group-append" v-on:click="togglePopover">
              <span class="input-group-text">
                <em class="fa fa-calendar"></em>
              </span>
            </div>
          </template>
        </vc-date-picker>
      </div>
    </div>
  
    <div class="col-md-6 ">
            <div class="">{{username }}</div>
            <div class="text-sucess" style="color:green">Shift: {{starttime }} To {{ endtime }}</div> 
            <div class="d-flex flex-row">
          <div class="dropdown pl-2 rounded" style="background: #008ff5;">
            <em class="fa fa-eye text-white" aria-hidden="true"></em>
            <select @change="handleUserChange" class="btn pl-0 dropdown-toggle text-white"  v-model="User" type="button" id="dropdownMenu2" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <option class="text-dark" v-for="users in ddlUser" :key="users.id" :value="users.id">
                 {{ users.Name }}
             </option>
            </select>
          </div>
        </div>
        </div>
    <div class="row d-flex justify-content-between mx-0 my-3">
      <div class="col-2 border rounded light-blue-border card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
          <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/Arrival-Time.png">
        </div>
        <div class="col-md-8">
          <span>{{$t('ArrivalTime')}}</span>
          <h5 class="text-primary"> {{ $options.filters.formatTime(ArrivalTime) }}</h5>
        </div>
      </div>
      <div class="col-2 border rounded border-primary card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
          <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/left-time.png">
        </div>

        <div class="col-md-8">
          <span>{{$t('LeftTime')}}</span>
           <h5 class="text-waring">{{ $options.filters.formatTime(AverrageLeftTime) }}</h5>
        </div>
      </div>
      <div class="col-2 border rounded border-success card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
          <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/productive-time.png">
        </div>
        <div class="col-md-8">
          <span>{{$t('ProductTime')}}</span>
          <h5 class="text-succes">{{ ProductiveTime }}</h5>
        </div>
      </div>
      <div class="col-2 border rounded dark-blue-border card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
          <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/Time-at-Work.png">
        </div>
        <div class="col-md-8">
         <span>{{$t('TimeofWork')}}</span>
         <h5 class="text-primary">{{ DurationTime }}</h5>
        </div>
      </div>
      <div class="col-2 border rounded border-success card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
            <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/productivity.png">
        </div>
        <div class="col-md-8">
          <span>{{$t('Productivity')}}</span>
          <h5 class="text-succes"> {{ ProductivityPercentage }}</h5>
        </div>
      </div>
      <div class="col-2 border rounded red-orange-border card-boxes d-flex justify-content-center align-items-center">
        <div class="col-md-4 text-center">
          <img class="py-4" src="https://talygenv.github.io/Vue-UI-Content/images/idle-time.png">
        </div>
        <div class="col-md-8">
          <span>{{$t('IdleTime')}}</span>
          <h5 style="color: #fc5a03;">{{ IdleTime }}</h5>
        </div>
      </div>
    </div>
    <div class="row-12">
      <VueApexCharts v-if="isShowProductivityBar" width="100%" height="500px" type="bar" :options="options"
        :series="series"></VueApexCharts>
    </div>
    <div class="row d-flex justify-content-between mx-1">
      <!-- ProductiveApps -->
      <div class="col-3 border productive-box p-0">
        <h6 class="border-bottom p-2">{{$t('ProductiveApps')}}<span class="float-right">{{ ProductiveTotalTime }}</span></h6>
        <div v-if="ProductiveApps.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{$t('No_Record_Found')}}</p>
        </div>
        <div class="row ml-2" v-for="(app, index) in TempProductiveApps" :key="index">
          <div :class="app.icon_class ? 'appicon ' + app.icon_class : 'appicon default'"></div>
          <div class="col-md-4" :title="app.application_display_name">
            {{ TruncateStr(app.application_display_name, 10) }} <br />
            <small>{{ TruncateStr(app.category_name, 15) }}</small>
          </div>
          <div class="col-md-4">
            <small class="text-center" :style="{ color: app.progress_bar_options.progress.color }">{{
              app.percentage_duration }}%</small>
            <br />
            <ProgressBar :options="app.progress_bar_options" :value="app.percentage_duration" />
          </div>
          <div class="col-md-3 text-right">
            <small>{{ app.duration_time }}</small>
          </div>
        </div>
        <h6 v-if="ProductiveApps.length > 4 && isShowViewAllProductive" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="ViewAllApps('productive')">
          {{$t('ViewAll')}}
        </h6>
      </div>
      <!-- Unproductive Apps  -->
      <div class="col-3 border productive-box p-0">
        <h6 class="border-bottom p-2">{{$t('UnproductiveApps')}} <span class="float-right">{{ UnProductiveTotalTime }}</span></h6>
        <div v-if="UnProductiveApps.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{$t('No_Record_Found')}}</p>
        </div>
        <div class="row ml-2" v-for="(app, index) in TempUnProductiveApps" :key="index">
          <div :class="app.icon_class ? 'appicon ' + app.icon_class : 'appicon default'"></div>
          <div class="col-md-4" :title="app.application_display_name">
            {{ TruncateStr(app.application_display_name, 10) }} <br />
            <small>{{ app.category_name }}</small>
          </div>
          <div class="col-md-4">
            <small class="text-center" :style="{ color: app.progress_bar_options.progress.color }">{{
              app.percentage_duration }}%</small>
            <br />
            <ProgressBar :options="app.progress_bar_options" :value="app.percentage_duration" />
          </div>
          <div class="col-md-3 text-right">
            <small>{{ app.duration_time }}</small>
          </div>
        </div>
        <h6 v-if="UnProductiveApps.length > 4 && isShowViewAllUnProductive" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="ViewAllApps('unproductive')">
           {{$t('ViewAll')}}
        </h6>
      </div>
      <!-- Neutral Apps -->
      <div class="col-3 border productive-box p-0">
        <h6 class="border-bottom p-2">
           {{$t('NeutralApps')}}<span class="float-right">{{ NeutralTotalTime }}</span>
        </h6>
        <div v-if="UnProductiveApps.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{$t('No_Record_Found')}}</p>
        </div>
        <div class="row ml-2" v-for="(app, index) in TempNeutralApps" :key="index">
          <div :class="app.icon_class ? 'appicon ' + app.icon_class : 'appicon default'"></div>
          <div class="col-md-4" :title="app.application_display_name">
            {{ TruncateStr(app.application_display_name, 10) }} <br />
            <small>{{ app.category_name }}</small>
          </div>
          <div class="col-md-4">
            <small class="text-center" :style="{ color: app.progress_bar_options.progress.color }">{{
              app.percentage_duration }}%</small>
            <br />
            <ProgressBar :options="app.progress_bar_options" :value="app.percentage_duration" />
          </div>
          <div class="col-md-3 text-right">
            <small>{{ app.duration_time }}</small>
          </div>
        </div>
        <h6 v-if="NeutralApps.length > 4 && isShowViewAllNeutral" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="ViewAllApps('neutral')">
           {{$t('ViewAll')}} <em class="fa fa-angle-down" aria-hidden="true"></em>
        </h6>
        <h6 v-if="isShowViewAllNeutral==false" class="border-top p-2 text-center"
          style="cursor: pointer" v-on:click="collapse('neutral')">
           {{$t('Collapse')}} <em class="fa fa-angle-up" aria-hidden="true"></em>
        </h6>
      </div>
      <div class="col-3 border productive-box p-0">
        <h6 class="border-bottom p-2">{{$t('MostUsedApps')}}</h6>
        <div v-if="mostusedAppsData.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{$t('No_Record_Found')}}</p>
        </div>
        <div id="chart">
          <VueApexCharts v-if="mostuserappdata" type="bar" height="300" :options="chartOptions" :series="series1">
          </VueApexCharts>
        </div>
      </div>
    </div>
    <br />
    <!-- Catagories -->
    <div class="row d-flex justify-content-between mx-2">
      <div class="col-12 border">
        <h6 class="mt-2">{{$t('Categories')}}</h6>
        <div v-if="AppDurationByCategories.length === 0" class="text-center mt-4 error-message">
          <p class="error-text">{{$t('No_Record_Found')}}</p>
        </div>
        <span v-for="(item, index) in AppDurationByCategories" :key="index">
          <small class="colorboxsquare" v-bind:style="{ background: item.color_code }"></small>
          <small class="mr-3">{{ item.category_name }}</small>
        </span>
        
    <div class="progress" >
      <div class="progress-bar" v-for="item in AppDurationByCategories" style="margin-right: 3px;" :style="{ width: item.percentage_duration + '%' }">
      <div class="center h6" style="justify-content: center;margin-bottom: 0;" v-bind:style="{ background: item.color_code }" :key="index" role="progressbar"  :aria-valuenow="item.percentage_duration" aria-valuemin="0" aria-valuemax="100">
          {{ item.duration_time}}
      </div>
      </div>
    </div>
      </div>
    </div>
  </section>
</template>
<script>
var TimeRange = [];
var dateType = "DD";
import VueApexCharts from "vue-apexcharts";
import DataService from "../../services/DataService";
import ProgressBar from "vuejs-progress-bar";
export default {
  components: { VueApexCharts, ProgressBar },
  data() {
    return {
      User: "1",
      ddlUser: [
        {
          id: "1",
          Name: "My View",
        },
        {
          id: "2",
          Name: "Department View",
        },
      ],
      //Start Productive bar line bar graph
      isShowProductivityBar: false,
      mostuserappdata: false,
      options: {
        colors: ["#3c8028","#FFA500","#84857f","#ededed"],
        chart: {
          type: 'bar',
          height: 400,
          stacked: true,
          toolbar: {
            show: false
          },
          zoom: {
            enabled: true
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "100%",
          }
        },
        dataLabels: { enabled: false },
        stroke: {
          width: 1,
          colors: ["#fff"]
        },
        title: {
          text: "Productivity Bar"
        },
        xaxis: {
          categories: [],
          labels: {
            formatter: function (value) {
              if (dateType == "DD") {
                for (var item in TimeRange) {
                  if (TimeRange[item].length == 7)
                    TimeRange[item] = "0" + TimeRange[item];
                  if (value == TimeRange[item]){
                    value =value.replace(':00','');
                    if(value[0]=='0'){
                      value=value.substring(1);
                    }
                    return value;
                  }
                }
              } 
              else if (dateType == "WK") {

                if (value == 'Monday 00:00:00' || value == 'Tuesday 00:00:00'|| value == 'Wednesday 00:00:00'|| value == 'Thursday 00:00:00'|| value == 'Friday 00:00:00'|| value == 'Saturday 00:00:00'|| value == 'Sunday 00:00:00')
                {
                  value =value.replace(/ 00:00:00/,'');
                }
                else
                {
                  value="";
                }
                return value;
                }
              else {
                return value;
              }
            },
          },        
        },
        tooltip: {
          enabled: true,
          y: {
            formatter: function (value) {
              if (value === undefined) {
                return ''; // Return empty string to hide the tooltip
              }
              else if( dateType == "DD") {
              // Customize the tooltip content here
              return value;
              }
              else if( dateType == "Wk") {
              // Customize the tooltip content here
              return value;
              }
              else if( dateType == "MM") {
              // Customize the tooltip content here
              return 'Arrival Time:'+ value 
              + '</br>Left Time:'
              + '</br>Idle Time:'
              + '</br>Productivity(%):' ;
              }
              
             
            },
            title: {
            formatter: function (seriesName) {
              if( dateType == "DD") {
                return seriesName
              }
              if( dateType == "WK") {
                return seriesName
              }
              if( dateType == "MM") {
                return ''
              }
            }
           }
          },
        },
        yaxis: {
          min: 0,
          max: 100
        },
        legend: { position: "top", horizontalAlign: "right", offsetX: 40 },
      },
      series: [
        { name: "Productive App", data: [] },
        { name: "Nuetral App", data: [] },
        { name: "Unproductive App", data: [] },
        { name: "No Record", data: [] },
      ],
     // colors: ["#3c8028","#FFA500","#84857f" ],
      fill: { type: "solid" },
      xaxis: { type: "" },
      //End Productive bar line bar graph
      //Start horizontal chart for Most Used apps
      chartOptions: {
        colors: ["#3c8028","#84857f","#FFA500" ],
        chart: { 
          type: "bar",
         height: 350 ,
         toolbar: {
            show: false
          },},
        plotOptions: { bar: { horizontal: true, endingShape: "rounded" } },
        dataLabels: { enabled: false },
        xaxis: {
          categories: [],
          labels: {
            // formatter: function (value) {
            //   return (value * 0.166).toFixed(0) + "%";
            // },
          },
        },
        fill: { opacity: 1 },
        legend: { position: "top", horizontalAlign: "right", offsetX: 40 },
        // fill: { opacity: 1 },
        // legend: { position: "top", horizontalAlign: "right", offsetX: 40 },
      },
      series1: [{ name: "Productive App", data: []} ,
       { name: "Unproductive App", data: []},
        { name: "Nuetral App", data: [] },
        ],
      colors: ["#3c8028","#84857f","#FFA500" ], 
      //Start horizontal chart for Most Used apps
      userId: "",
      fromDate: "",
      ToDate: "",
      ChartXAxis: "",
      ChartYAxis: "",
      timedata: [],
      ArrivalTime: "",
      AverrageLeftTime: "",
      DurationTime: "",
      duration: "",
      dateType: "DD",
      weeklyFromDate: "",
      startDate: new Date(),
      endDate: new Date(),
      monthStartDate: new Date(),
      monthEndDate: new Date(),
      weekDate: {
        start: this.startDate,
        end: this.endDate,
      },
      monthRange: {
        start: this.monthStartDate,
        end: this.monthEndDate,
      },
      updateMonthRange: false,
      isLoading: false,
      ProductivityPercentage: null,
      IdleTime: null,
      ProductiveTime: null,
      ProductiveApps: [],
      TempProductiveApps: [],
      UnProductiveApps: [],
      TempUnProductiveApps: [],
      NeutralApps: [],
      TempNeutralApps: [],
      categories: [],
      isShowViewAllProductive: true,
      isShowViewAllUnProductive: true,
      isShowViewAllNeutral: true,
      ProductiveTotalTime: null,
      UnProductiveTotalTime: null,
      NeutralTotalTime: null,
      AppDurationByCategories: [],
      mostusedAppsData: [],
      username:"",
      starttime:"",
      endtime:""
    };
  },
  async created() {
    debugger
    var vm = this;
    vm.userId = vm.$route.params.id;
    vm.GetArrivalTime();
    vm.GetUserNameandShift();
    dateType = "DD";
    vm.fromDate = new Date();
    vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
    vm.ToDate = vm.fromDate + " 23:59:00";
    vm.formatDuration();
    TimeRange = vm.getTimeRanges(60);
    var todayDate = new Date();
    var currentMonthDays = new Date(
      todayDate.getFullYear(),
      todayDate.getMonth() + 1,
      0
    ).getDate();
    var dateBefore7Days = new Date();
    dateBefore7Days.setDate(dateBefore7Days.getDate() - 6);
    var dateBeforeThirtyDays = new Date();
    dateBeforeThirtyDays.setDate(
      dateBeforeThirtyDays.getDate() - currentMonthDays
    );
    vm.monthEndDate = dateBeforeThirtyDays;
    vm.endDate = dateBefore7Days;
    vm.weekDate = {
      start: vm.startDate,
      end: vm.endDate,
    };
    vm.monthRange = {
      start: vm.monthStartDate,
      end: vm.monthEndDate,
    };
    await vm.GetTimerDashboard();
    await vm.GetAllApps();
    await vm.GetMostUsedApps();
    await vm.GetUsedAppTimeDurationByCategories();
  },
  updated() {
    var vm = this;
    const newDate = vm.ConvertDateFormat(new Date());
    if (vm.dateType == "WK") {
      var dateBefore7Days = new Date();
      dateBefore7Days.setDate(dateBefore7Days.getDate() - 6);
      const startDate = vm.ConvertDateFormat(vm.startDate);
      const endDate = vm.ConvertDateFormat(vm.endDate);
      const dateBeforeSevenDays = vm.ConvertDateFormat(dateBefore7Days);
      if (!(startDate == newDate && endDate == dateBeforeSevenDays)) {
        const sevenDaysLater = new Date(vm.startDate);
        sevenDaysLater.setDate(vm.startDate.getDate() + 6);
        const diffTime = Math.abs(vm.endDate - vm.startDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        if (vm.endDate > sevenDaysLater || diffDays < 6) {
          vm.weekDate = {
            start: vm.startDate,
            end: sevenDaysLater,
          };
        }
      }
    } else if (vm.dateType == "MM") {
      var todayDate = new Date();
      const dateBefore30Days = new Date();
      var currentMonthDays = new Date(
        todayDate.getFullYear(),
        todayDate.getMonth() + 1,
        0
      ).getDate();
      dateBefore30Days.setDate(dateBefore30Days.getDate() - currentMonthDays);
      const dateBeforeThirtyDays = vm.ConvertDateFormat(dateBefore30Days);
      const monthStartDate = vm.ConvertDateFormat(vm.monthStartDate);
      const monthEndDate = vm.ConvertDateFormat(vm.monthEndDate);
      const thirtyDaysLater = new Date(vm.monthStartDate);
      thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);
      if (
        !(monthStartDate == dateBeforeThirtyDays && monthEndDate == newDate)
      ) {
        const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        if (vm.monthStartDate > thirtyDaysLater || diffDays < 31) {
          vm.monthRange = {
            start: vm.monthStartDate,
            end: thirtyDaysLater,
          };
        }
      } else if (vm.updateMonthRange) {
        vm.monthRange = {
          start: vm.monthStartDate,
          end: thirtyDaysLater,
        };
      }
    }
  },

  methods: {
    GetUserNameandShift(){ 
      var vm = this;
      var userid;
      if (vm.$route.params.id == undefined ) {
        vm.logginuserID=vm.GetUserInfo.ID;
        userid=vm.logginuserID
      }
      else{
        userid=vm.$route.params.id
      }
      var params = `userId=${userid}`;
      DataService.GetUserNameandshift(params).then((response) => {
       vm.username = response.data.data[0].USERNAME
       vm.starttime = response.data.data[0].TS[0].STARTTIME
       vm.endtime = response.data.data[0].TS[0].ENDTIME
      });
    },
    ConvertDateFormat(date) {
      var newDate = new Date(date);
      var mnth = ("0" + (newDate.getMonth() + 1)).slice(-2);
      var day = ("0" + newDate.getDate()).slice(-2);
      var datavalue = [newDate.getFullYear(), mnth, day].join("-");
      return datavalue;
    },
    selectToday: function (reqFrom) {
      var vm = this;
      vm.dateType = "DD";
      dateType = "DD";
      vm.fromDate = new Date();
      vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
      vm.ToDate = vm.fromDate + " 23:59:00";
      vm.GetTimerDashboard();
    },
    selectWeekly: function () {
      var vm = this;
      vm.dateType = "WK";
      dateType = "WK";
      var dt = new Date(); //current date of week
      var currentWeekDay = dt.getDay();
      var lessDays = currentWeekDay == 0 ? 6 : currentWeekDay - 1;
      var wkStart = new Date(new Date(dt).setDate(dt.getDate() - lessDays));
      var wkEnd = new Date(new Date(wkStart).setDate(wkStart.getDate() + 6));
      vm.weeklyFromDate = wkStart;
      vm.ToDate = wkEnd;
      vm.ToDate = vm.ConvertDateFormat(vm.ToDate);
      vm.weeklyFromDate = vm.ConvertDateFormat(vm.weeklyFromDate);
    },
    selectMonthly: function () {
      var vm = this;
      vm.options.xaxis.categories = []; //clear the array
      vm.series[0].data = [];
      vm.series[1].data = [];
      vm.series[2].data = [];
      vm.dateType = "MM";
      dateType = "MM";
      vm.fromDate = "";
      var date = new Date(),
        y = date.getFullYear(),
        m = date.getMonth();
      var firstDay = new Date(y, m, 1);
      var lastDay = new Date(y, m + 1, 0);
      vm.fromDate = firstDay;
      vm.ToDate = lastDay;
      vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
      vm.ToDate = vm.ConvertDateFormat(vm.ToDate);
    },
    GetTimerDashboard: async function () {
      var VueObj = this;
      VueObj.options.xaxis.categories = []; //clear the array
      VueObj.series[0].data = [];
      VueObj.series[1].data = [];
      VueObj.series[2].data = [];
      VueObj.series[3].data = [];
      var param = "";
      if (VueObj.dateType == "DD") {
        param = `fromDate=${VueObj.fromDate}&toDate=${VueObj.ToDate}&type=${VueObj.dateType}&userIds=${VueObj.userId == undefined ? '' : VueObj.userId}&viewType=MyView`;
      } else if (VueObj.dateType == "WK") {
        param = `fromDate=${VueObj.ConvertDateFormat(VueObj.startDate)}&toDate=${VueObj.ConvertDateFormat(VueObj.endDate)}&type=${VueObj.dateType}&userIds=${VueObj.userId == undefined ? '' : VueObj.userId}&viewType=MyView`;
      } else {
        param = `fromDate=${VueObj.ConvertDateFormat(VueObj.monthStartDate)}&toDate=${VueObj.ConvertDateFormat(VueObj.monthEndDate)}&type=${VueObj.dateType}&userIds=${VueObj.userId == undefined ? '' : VueObj.userId}&viewType=MyView`;
      }
      VueObj.getgraphdata(param);
    },
    getgraphdata(param) {
      var VueObj = this;
      var getvalue = '';
      VueObj.isLoading = true;
      VueObj.isShowProductivityBar = false;
      DataService.TimerDashboard(param).then((response) => {
        VueObj.timedata = response.data.data;
        if (VueObj.timedata){
          VueObj.timedata.forEach((itm) => {
            if(getvalue != itm.TimeIntervalAMPM){
            getvalue = itm.TimeIntervalAMPM;
            VueObj.options.xaxis.categories.push(itm.TimeIntervalAMPM);
            VueObj.series[0].data.push(itm.ProductivePer);
            VueObj.series[1].data.push(itm.UnProductivePer);
            VueObj.series[2].data.push(itm.NeutralPer);
            VueObj.series[3].data.push(itm.NoRecord);
           }
          });
          VueObj.isLoading = false;
          VueObj.isShowProductivityBar = true;
        }
      }).catch(error => console.log(error));
    },
    handleUserChange() {
      if (this.User === "2") {
        this.$router.push({ path: "/TimeTracking/DepartmentViewDasboard" });
      }
    },
    GetArrivalTime: function () {
      var vueObj = this;
      var param = "";
      if (vueObj.dateType == "DD") {
        param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&type=MyView&userIds=${vueObj.userId}`;
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&type=MyView&userIds=${vueObj.userId}`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&type=MyView&userIds=${vueObj.userId}`;
      }
      DataService.getArrivalTimeDashboard(param).then((response) => {
        vueObj.ArrivalTime = response.data.data[0][0].startTime;
        vueObj.AverrageLeftTime = response.data.data[0][0].endTime;
        vueObj.DurationTime = response.data.data[0][0].ScreenCaptureTime;
        vueObj.ProductivityPercentage =
          response.data.data[0][0].productivityPercentage;
        vueObj.IdleTime = response.data.data[0][0].IdleTime;
        vueObj.ProductiveTime = response.data.data[0][0].ProductiveTime;
      });
    },
    formatDuration(durationInHours) {
      const hours = Math.floor(durationInHours);
      const minutes = Math.round((durationInHours - hours) * 60);
      return `${hours}h ${minutes}m`;
    },
    async onDateChange(event) {
      var vm = this;
      if (vm.dateType == "DD") {
        vm.fromDate = new Date(event);
        vm.fromDate = vm.ConvertDateFormat(vm.fromDate);
        vm.ToDate = vm.fromDate + " 23:59:00";
        await vm.GetTimerDashboard();
        await vm.GetAllApps();
        await vm.GetUsedAppTimeDurationByCategories();
        await vm.GetMostUsedApps();
        vm.GetArrivalTime();
      }
    },
    async onWeekDateChange(date) {
      var vm = this;
      vm.startDate = date.start;
      vm.endDate = date.end;
      const sevenDaysLater = new Date(vm.startDate);
      sevenDaysLater.setDate(vm.startDate.getDate() + 6);
      const diffTime = Math.abs(vm.endDate - vm.startDate);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      if (diffDays < 6) {
        vm.ShowAlert(
          "Please select atleast seven days.",
          "warning",
          false,
          vm.$t("Alert")
        );
        return false;
      }
      if (vm.endDate > sevenDaysLater) {
        vm.ShowAlert(
          "You can not select more than seven days.",
          "warning",
          false,
          vm.$t("Alert")
        );
        return false;
      }
      await vm.GetTimerDashboard();
      await vm.GetAllApps();
      await vm.GetUsedAppTimeDurationByCategories();
      await vm.GetMostUsedApps();
      vm.GetArrivalTime();
    },
    async onMonthSelected(date) {
      var vm = this;
      vm.updateMonthRange = false;
      vm.monthStartDate = date.start;
      vm.monthEndDate = date.end;
      var todayDate = new Date();
      var currentMonthDays = new Date(
        todayDate.getFullYear(),
        todayDate.getMonth() + 1,
        0
      ).getDate();
      const thirtyDaysLater = new Date(vm.monthStartDate);
      thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);
      const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      var dateBeforeThirtyDays = new Date();
      dateBeforeThirtyDays.setDate(
        dateBeforeThirtyDays.getDate() - currentMonthDays
      );
      if (diffDays < 27) {
        vm.updateMonthRange = true;
        vm.ShowAlert(
          "Please select atleast 30 days.",
          "warning",
          false,
          vm.$t("Alert")
        );
        vm.monthEndDate = new Date();
        vm.monthStartDate = dateBeforeThirtyDays;
      } else if (vm.monthEndDate > thirtyDaysLater) {
        vm.updateMonthRange = true;
        vm.ShowAlert(
          "You can not select more than 30 days.",
          "warning",
          false,
          vm.$t("Alert")
        );
        vm.monthEndDate = new Date();
        vm.monthStartDate = dateBeforeThirtyDays;
      }
      await vm.GetTimerDashboard();
      await vm.GetAllApps();
      await vm.GetUsedAppTimeDurationByCategories();
      await vm.GetMostUsedApps();
      vm.GetArrivalTime();
    },
    getTimeRanges(interval) {
      const ranges = [];
      const date = new Date();
      const format = {
        hour: "numeric",
        minute: "numeric",
      };
      for (let minutes = 0; minutes < 24 * 60; minutes = minutes + interval) {
        date.setHours(0);
        date.setMinutes(minutes);
        ranges.push(date.toLocaleTimeString("en", format));
      }
      return ranges;
    },
    togglePopover() { },
