    GetMostUsedApps: async function () {
      var vueObj = this;
      var param = "";
      vueObj.chartOptions.xaxis.categories = [];
      vueObj.series1[0].data = [];
      vueObj.mostuserappdata = false;
      if ((vueObj, dateType == "DD")) {
        param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&type=${vueObj.dateType}&userIds=${vueObj.userId == undefined ? '' : vueObj.userId}&viewType=MyView`;
